.App {
  display:flex;
  align-items: center;
  justify-content: center;
  background-color: #000;
  width: auto;
  height: auto;
  color: #fff;
  /* overflow-y: scroll; */
}

.table{
  display: flex;
  flex-direction: column;
  gap: 20px;
  border: 1px solid #fff;
  width: 98%;
  flex-wrap: wrap;
  
}

.table_header{
  display: flex;
  flex-direction: row;
  border: 1px solid #fff;
  width: calc( 100% - 50px);
  padding: 25px;
  gap: 10px;
}

.img{
  width: 20px;
  height: 20px;
}

.table_content{
  display: flex;
  flex-direction: row;
  padding: 25px;
  gap: 10px;
}

 td{
  display: flex;
  align-items: flex-start;
  width: 125px;
  height: 20px;
  overflow: hidden;
}

